@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Comfortaa&family=Josefin+Sans:ital,wght@0,400;1,300&family=Ubuntu:ital,wght@1,500&display=swap);
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Balsamiq Sans', cursive;
}

.main_div{
    width: 100%;
    height: 100vh;
    background: #7bed9f;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

.center_div{
    width: 25%;
    height: 70vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
    border-radius: 15px;
}

/* Heading CSS */

h1{
    color: rgba(0,0,0,0.87);
    font-size:50px;
    margin-bottom: 30px;
    min-width: 64px;
    box-sizing: border-box;
    font-weight: 500;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    text-shadow: 5px 5px 5px rgba(0,0,0,0.3);
}

/* Input fild CSS */

input{
    text-align: center;
    height: 30px;
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    width: 60%;
    border-bottom: 2px solid #8566aa;
    outline: none;
    margin-right: 20px;
}

/* Add Button CSS  */

.newBtn{
    width: 50px;
    height: 55px;
    background-color: #16a085 !important;
    color: white !important;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
    border-radius: 50% !important;
}

.newBtn:hover{
    color: #16a085 !important;
    background-color: white !important;
}

ol{
    margin-top: 30px;
}

ol li{
    padding-left: 0px;
    text-align: left;
    font-size: 20px;
    font-weight: 500;
    min-height: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #8566aa;
    text-transform: capitalize;
    list-style: none;
}

/* todo style CSS */
.todo_style{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: left;
            justify-content: left;
    -webkit-align-items: center;
            align-items: center;
}

/* delete icon CSS */
.todo_style .deleteIcon{
    width: 25px;
    height: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #8566aa;
    border-radius: 50%;
    margin: 0px 15px 0px 35px;
    padding: 5px;
    color:aliceblue;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

.todo_style .deleteIcon:hover{
    background: transparent;
    color: crimson;
    box-shadow: 5px 5px 15px -5px rgba(225,0,0,0.5);
}

.todo_style .fa-times:hover .list_style{
    color: crimson;
}

@media (max-width: 768px){
    .center_div{
        width: 55%;
    }

    .todo_style .fa-times{
        margin: 0 15px 0 1px;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }

    button{
        margin-left: 5px;
        min-height: 40px;
        width: 40px;
        color: #fff;
        font-size: 30px;
    }
}
@media only screen and (max-width: 600px) {
  .main_div {
    width: 100%;
    height: 100vh;
    background: #7bed9f;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
  }

  /* center Div CSS */
  .center_div {
    width: 85%;
    height: 75vh;
  }

  /* Heading CSS */
  h1 {
    font-size: 38px;
    margin-bottom: 15px;
    box-sizing: border-box;
  }

  /* Input fild CSS */
  input {
    height: 30px;
    font-size: 16px;
  }

  ol {
    margin-top: 30px;
  }

  ol li {
    min-height: 36px;
  }

  /* delete icon CSS */
  .todo_style .deleteIcon {
    margin: 0px 15px 0px 0px;
  }
}

